body {
  color: black;
  background: #cbd4d6;
  max-width: 100%;
  margin: 0px auto; }

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box; }

.nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background-color: #adb6ad;
  font-family: "Poppins", sans-serif; }
  .nav__logo {
    display: flex;
    align-items: center;
    text-transform: uppercase;
    letter-spacing: 5px;
    font-size: 25px; }
  .nav__homepage {
    color: #cee4e9; }
  .nav__links {
    display: flex;
    width: 45%;
    justify-content: space-around; }
  .nav__list {
    list-style: none; }
  .nav__link {
    color: black;
    text-decoration: none;
    letter-spacing: 3px;
    font-weight: bold;
    font-size: 16px; }
  .nav__burgerMenu {
    cursor: pointer;
    display: none; }
  .nav__burgerLine {
    width: 25px;
    height: 3px;
    margin: 5px;
    background-color: black;
    transition: all 0, 3s ease-in; }
  @media screen and (min-width: 641px) {
    .nav__links {
      width: 65%; } }
  @media screen and (max-width: 642px) {
    .nav body {
      overflow-x: hidden; }
    .nav__links {
      position: absolute;
      height: 55vh;
      top: 10vh;
      background-color: #d6cbd2;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 75%;
      transition: transform 0.5s ease-in;
      transform: translateX(-120%); }
    .nav__list {
      opacity: 0; }
    .nav__active {
      transform: translateX(-17%); }
    .nav__burgerMenu {
      display: block; }
    .nav .toggle .nav__burgerLine1 {
      transform: rotate(-45deg) translate(-5px, 6px); }
    .nav .toggle .nav__burgerLine2 {
      opacity: 0; }
    .nav .toggle .nav__burgerLine3 {
      transform: rotate(45deg) translate(-5px, -6px); } }

@keyframes navLinkFade {
  from {
    opacity: 0;
    transform: translateX(50px); }
  to {
    opacity: 1;
    transform: translateX(0px); } }

